.ConnectedTestItem {
	@apply w-96 max-w-sm relative flex flex-row items-center justify-between mt-3 p-2 focus:outline-none bg-slate-100 hover:bg-slate-200 text-gray-600 hover:text-gray-800 hover:scale-[1.02] transition-all rounded-md;
	.name {
		@apply font-semibold;
	}
	.date {
		@apply font-normal text-sm text-gray-400;
	}
}
