.CreateQuestionModal {
	@apply h-screen w-screen bg-black bg-opacity-70 fixed flex items-center justify-center top-0 left-0 z-20;
	.content {
		@apply p-12 rounded-2xl w-[50%] bg-slate-50 relative;
		.title {
			@apply flex items-center justify-center mb-4 text-xl font-semibold;
		}
		.close {
			@apply flex justify-end items-center;
		}
		.createForm {
			@apply flex items-center justify-center w-full;
		}
		.choseRole {
			@apply w-full mb-2 bg-transparent outline-none focus:outline-none px-3 py-2 text-lg rounded-lg border hover:bg-gray-200;
		}
	}
}
